import React, { useState } from 'react';
import { Form, Button, Card, Alert, Spinner } from 'react-bootstrap';
import { api } from '../services/api';

function HeatmapPage() {
  const [formData, setFormData] = useState({
    dataset_id: '',
    gene_list: '',
    n_genes: 50,
    clustering_method: 'hierarchical'
  });
  const [results, setResults] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError(null);

    try {
      const response = await api.runDifferentialExpression(formData);
      if (response.ok) {
        const data = await response.json();
        setResults(data.results);
      } else {
        setError('');
      }
    } catch (err) {
      setError(': ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: name === 'dataset_id' || name === 'n_genes' 
        ? parseInt(value) : value
    }));
  };

  return (
    <div>
      <h2> </h2>
      <p className="text-muted"></p>

      <Card className="mb-4">
        <Card.Header>
          <h5></h5>
        </Card.Header>
        <Card.Body>
          <Form onSubmit={handleSubmit}>
            <Form.Group className="mb-3">
              <Form.Label>ID</Form.Label>
              <Form.Control
                type="number"
                name="dataset_id"
                value={formData.dataset_id}
                onChange={handleInputChange}
                placeholder="ID"
                required
              />
            </Form.Group>
            <Form.Group className="mb-3">
              <Form.Label> ()</Form.Label>
              <Form.Control
                as="textarea"
                rows={3}
                name="gene_list"
                value={formData.gene_list}
                onChange={handleInputChange}
                placeholder=": TP53, BRCA1, MYC"
              />
              <Form.Text className="text-muted">
                N
              </Form.Text>
            </Form.Group>
            <Form.Group className="mb-3">
              <Form.Label></Form.Label>
              <Form.Control
                type="number"
                name="n_genes"
                value={formData.n_genes}
                onChange={handleInputChange}
                min="10"
                max="500"
              />
            </Form.Group>
            <Form.Group className="mb-3">
              <Form.Label></Form.Label>
              <Form.Select
                name="clustering_method"
                value={formData.clustering_method}
                onChange={handleInputChange}
              >
                <option value="hierarchical"></option>
                <option value="kmeans">K-means</option>
                <option value="none"></option>
              </Form.Select>
            </Form.Group>
            <Button variant="primary" type="submit" disabled={loading}>
              {loading ? (
                <>
                  <Spinner size="sm" className="me-2" />
                  ...
                </>
              ) : (
                ''
              )}
            </Button>
          </Form>
        </Card.Body>
      </Card>

      {error && <Alert variant="danger">{error}</Alert>}

      {results && (
        <Card>
          <Card.Header>
            <h5></h5>
          </Card.Header>
          <Card.Body>
            {results.heatmap_image ? (
              <div className="text-center">
                <img 
                  src={`data:image/png;base64,${results.heatmap_image}`}
                  alt=""
                  className="img-fluid"
                  style={{ maxHeight: '600px' }}
                />
              </div>
            ) : (
              <div className="text-center text-muted">
                <p>...</p>
                <Spinner animation="border" />
              </div>
            )}
            <div className="mt-3">
              <h6>:</h6>
              <ul>
                <li>: {results.n_genes} </li>
                <li>: {results.n_samples} </li>
                <li>: {formData.clustering_method}</li>
              </ul>
            </div>
          </Card.Body>
        </Card>
      )}
    </div>
  );
}

export default HeatmapPage;
