import React, { useState } from 'react';
import { Form, Button, Card, Table, Alert, Spinner } from 'react-bootstrap';
import { api } from '../services/api';

function GeneQueryPage() {
  const [query, setQuery] = useState('');
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleSearch = async (e) => {
    e.preventDefault();
    if (!query.trim()) return;

    setLoading(true);
    setError(null);

    try {
      const response = await api.searchGenes(query);
      if (response.ok) {
        const data = await response.json();
        setResults(data);
      } else {
        setError('');
      }
    } catch (err) {
      setError(': ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      <h2> </h2>
      <p className="text-muted"></p>

      <Card className="mb-4">
        <Card.Body>
          <Form onSubmit={handleSearch}>
            <Form.Group className="mb-3">
              <Form.Label></Form.Label>
              <Form.Control
                type="text"
                placeholder=": TP53, BRCA1"
                value={query}
                onChange={(e) => setQuery(e.target.value)}
              />
            </Form.Group>
            <Button variant="primary" type="submit" disabled={loading}>
              {loading ? (
                <>
                  <Spinner size="sm" className="me-2" />
                  ...
                </>
              ) : (
                ''
              )}
            </Button>
          </Form>
        </Card.Body>
      </Card>

      {error && <Alert variant="danger">{error}</Alert>}

      {results.length > 0 && (
        <Card>
          <Card.Header>
            <h5> ({results.length} )</h5>
          </Card.Header>
          <Card.Body>
            <Table responsive striped hover>
              <thead>
                <tr>
                  <th>ID</th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {results.map((gene) => (
                  <tr key={gene.id}>
                    <td>
                      <code>{gene.gene_id}</code>
                    </td>
                    <td>
                      <strong>{gene.gene_symbol}</strong>
                    </td>
                    <td>{gene.gene_name}</td>
                    <td>{gene.chromosome}</td>
                    <td>
                      <span className="badge bg-info">
                        {gene.gene_type}
                      </span>
                    </td>
                  </tr>
                ))}
              </tbody>
            </Table>
          </Card.Body>
        </Card>
      )}
    </div>
  );
}

export default GeneQueryPage;
