import React, { useState, useEffect } from 'react';
import { Card, Row, Col, Spinner, Alert, Container, Jumbotron } from 'react-bootstrap';
import { api } from '../services/api';
import LoadingSpinner from '../components/LoadingSpinner';
import ErrorAlert from '../components/ErrorAlert';
import StatisticsCard from '../components/StatisticsCard';
import FeatureCard from '../components/FeatureCard';

function HomePage() {
  const [statistics, setStatistics] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchStatistics();
  }, []);

  const fetchStatistics = async () => {
    try {
      const response = await api.getStatistics();
      if (response.ok) {
        const data = await response.json();
        setStatistics(data);
      } else {
        setError('Failed to fetch statistics');
      }
    } catch (err) {
      setError('Network error: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return <LoadingSpinner message="..." size="lg" />;
  }

  if (error) {
    return <ErrorAlert error={error} onRetry={fetchStatistics} />;
  }

  return (
    <div>
      {/* Hero Section */}
      <div className="bg-gradient-primary text-white py-5 mb-5 rounded">
        <Container>
          <div className="text-center">
            <h1 className="display-3 fw-bold mb-3"> NETA</h1>
            <p className="lead fs-4 mb-4"></p>
            <p className="fs-5 opacity-75">
              19GEO
            </p>
            <div className="mt-4">
              <span className="badge bg-light text-dark me-2 fs-6">100%</span>
              <span className="badge bg-light text-dark me-2 fs-6"></span>
              <span className="badge bg-light text-dark fs-6"></span>
            </div>
          </div>
        </Container>
      </div>

      {/* Statistics Section */}
      {statistics && (
        <Row className="mb-5 g-4">
          <Col lg={3} md={6}>
            <StatisticsCard
              title=""
              value={statistics.total_datasets}
              subtitle="GEO"
              icon=""
              color="primary"
            />
          </Col>
          <Col lg={3} md={6}>
            <StatisticsCard
              title=""
              value={statistics.total_samples?.toLocaleString()}
              subtitle=""
              icon=""
              color="success"
            />
          </Col>
          <Col lg={3} md={6}>
            <StatisticsCard
              title=""
              value={statistics.total_genes?.toLocaleString()}
              subtitle=""
              icon=""
              color="warning"
            />
          </Col>
          <Col lg={3} md={6}>
            <StatisticsCard
              title=""
              value={`${(statistics.total_expressions / 1000000).toFixed(1)}M`}
              subtitle=""
              icon=""
              color="info"
            />
          </Col>
        </Row>
      )}

      {/* Features Section */}
      <Row className="mb-5 g-4">
        <Col lg={4} md={6}>
          <FeatureCard
            icon=""
            title=""
            description="PCA"
            features={[
              "DESeq2",
              "(PCA)",
              "KEGG/GO",
              "Kaplan-Meier",
              ""
            ]}
            color="primary"
          />
        </Col>
        <Col lg={4} md={6}>
          <FeatureCard
            icon=""
            title=""
            description=""
            features={[
              "",
              "",
              "",
              "",
              ""
            ]}
            color="success"
          />
        </Col>
        <Col lg={4} md={6}>
          <FeatureCard
            icon=""
            title="100%"
            description="GEO"
            features={[
              "19GEO",
              "2,196",
              "230,219",
              "380",
              ""
            ]}
            color="warning"
          />
        </Col>
        <Col lg={4} md={6}>
          <FeatureCard
            icon=""
            title=""
            description="Flask + React + RDocker"
            features={[
              "FlaskAPI",
              "React",
              "R",
              "Docker",
              "GitHub Pages"
            ]}
            color="info"
          />
        </Col>
        <Col lg={4} md={6}>
          <FeatureCard
            icon=""
            title=""
            description=""
            features={[
              "",
              "",
              "",
              "",
              ""
            ]}
            color="danger"
          />
        </Col>
        <Col lg={4} md={6}>
          <FeatureCard
            icon=""
            title=""
            description="GitHub Pages"
            features={[
              "",
              "",
              "",
              "",
              ""
            ]}
            color="primary"
          />
        </Col>
      </Row>

      {/* Data Sources Section */}
      <Card className="mb-5">
        <Card.Header className="bg-primary text-white">
          <h4 className="mb-0"> </h4>
        </Card.Header>
        <Card.Body>
          <Row>
            <Col md={6}>
              <h6></h6>
              {statistics?.tissue_types?.map((tissue, index) => (
                <div key={index} className="d-flex justify-content-between align-items-center mb-2">
                  <span>{tissue.name}</span>
                  <span className="badge bg-primary">{tissue.count}</span>
                </div>
              ))}
            </Col>
            <Col md={6}>
              <h6></h6>
              {statistics?.tumor_types?.map((tumor, index) => (
                <div key={index} className="d-flex justify-content-between align-items-center mb-2">
                  <span>{tumor.name}</span>
                  <span className="badge bg-success">{tumor.count}</span>
                </div>
              ))}
            </Col>
          </Row>
        </Card.Body>
      </Card>
    </div>
  );
}

export default HomePage;
